
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}
.modal-body {
  max-height: calc(100vh - 212px);
  overflow-y: auto;
  /* max-height: calc(100% - 120px);
  overflow-y: scroll; */
}



